.appContainer{
  display: flex;
  height: 100%;
  background-image: linear-gradient(to right, rgb(64, 92, 252), rgb(54, 52, 161));
  flex-direction: column;
}

.navContainer{
  display: flex;
  justify-content: space-between;
  background-image: linear-gradient(to right, rgba(0, 0, 0, .1), rgba(0, 0, 0, .15));
  color: white;
  height: 100px;
}

.logo{
  margin-left: 50px;
  cursor: pointer;
}

.navLink{
  text-decoration: none;
  color: inherit;
}

.navButtonContainer{
  display: flex;
  align-items: center;
  gap: 25px;
  margin-right: 50px;
}

.landButton{
  border: none;
  cursor: pointer;
  background-image: linear-gradient(to right, rgb(183, 106, 255), blueviolet);
  padding: 8px;
  border-radius: 15px;
  transition: transform 0.3s, box-shadow 0.15s;
  color: white;
}

.landButton:hover{
  background-image: linear-gradient(to right, rgb(157, 73, 236), rgb(118, 38, 194));
  transform: translateY(-3px);
}

.modalBackground{
  position: fixed;
  background-color: rgba(0, 0, 0, .7);
  background-size: cover;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modalContainer{
  background-color: white;
  padding: 40px;
  border-radius: 15px;
}

.closeButtonContainer{
  margin: 15px 15px 0px 0px;
  display: grid;
  justify-content: right;
}

.modalInputs{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 25px;
}

.modalButtons{
  margin-top: 40px;
}

.modalButtons button{
  margin-right: 25px;
  background-color: cornflowerblue;
  color: white;
  cursor: pointer;
  border: none;
  padding: 5px;
  border-radius: 10px;
}

.modalButtons button:hover{
  background-color: rgb(84, 128, 209);
}